import React, {Component} from 'react'
import '../../../stylesheets/partials/modules/Answer.scss';

class Answer extends Component {
  constructor(){
    super();
    this.state={
      localId: ''
    }
  }

  componentDidMount(){
    var talkBtn = document.querySelector(".replyContainer")
    var localId,START,END,recordTimer;
    function uploadVoice(){
      //
      //3
      wx.uploadVoice({
        localId: localId, // IDstopRecord
        isShowProgressTips: 1, // 1
        success: function (res) {
          console.log("")
          console.log("serverId is ==="+res.serverId)
          wx.playVoice({
            localId:localId
          })
          //idres.serverId
          // $.ajax({
          //   url: '',
          //   type: 'post',
          //   data: JSON.stringify(res),
          //   dataType: "json",
          //   success: function (data) {
          //     alert('');//
          //   },
          //   error: function (xhr, errorType, error) {
          //     console.log(error);
          //   }
          // });
        }
      });
    }

    var recordStartHandler = function (event) {
      event.preventDefault();
      START = new Date().getTime();
      talkBtn.classList.add('on')
      recordTimer = setTimeout(function(){
        wx.startRecord({
          success: function(){
            localStorage.rainAllowRecord = 'true';
          },
          cancel: function () {
            alert('');
          }
        });
      },300);
    }
    var recordStopHandler = function (event) {
      event.preventDefault();
      END = new Date().getTime();
      talkBtn.classList.remove('on');
      if((END - START) < 300){
        END = 0;
        START = 0;
        console.log(""+(END-START));
        //300ms
        clearTimeout(recordTimer);
      }else{
        console.log(""+(END-START));
        wx.stopRecord({
          success: function (res) {
            localId = res.localId
            console.log("")
            uploadVoice();
          },
          fail: function (res) {
            alert(JSON.stringify(res));
          }
        });
      }
      
    }
    var touchMoveHandler = function (event) {
      event.preventDefault();
    }
    talkBtn.addEventListener('touchstart',recordStartHandler)
    talkBtn.addEventListener('touchend',recordStopHandler)
    talkBtn.addEventListener('touchmove',touchMoveHandler)
  }
  render() {
    return (
      <div className="accountAnswer">
        <div className="question">
          <div className="head">
            <img src={require('../../../images/head.jpg')}/>
            <span className="name"></span>
            <span className="price"> 5.2</span>
          </div>
          <div className="stem"></div>
          <div className="time">15</div>
        </div>
        <div className="hint"> 0.3</div>
        <div className="replyHint"></div>
        <div className="replyContainer">
          <div className="replyIcon"></div>
          <div className="recording"></div>
        </div>
        <div className="reRecord"></div>
        <div className="recordHint">120S</div>
        <div className="sendBtn"></div>
      </div>
    )
  }

}

export default Answer;


